.pet {
  list-style: none;
  height: 250px;
  padding: 10px 0;
  margin: 3px 0;
  border: 1px solid #aaa;
  background: white;
  border-radius: 2px;
  cursor: pointer;
  transition: transform 250ms ease-in-out;

  &:hover {
    transform: translate(-1px, -2px);
    background: ghostwhite;
  }

  a {
    text-decoration: none;
    color: #888;
    
    &:visited {
      color: #999;
    }

    article {
      padding: 10px;
    }
  }
}


